html,
body,
div#__next,
div#__next > div {
  height: 100%; }

body {
  color: #495057;
  font-family: 'Sarabun', sans-serif;
  font-size: 20px; }

a,
a:visited {
  color: #175F9E;
  text-decoration: none; }

.social {
  animation-delay: 1.5s;
  background: radial-gradient(ellipse farthest-side at center, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.8) 50%, rgba(255, 255, 255, 0) 100%);
  color: #949da5;
  display: inline-block;
  font-size: 0.75rem;
  padding: 2rem; }
  @media only screen and (min-width: 768px) {
    .social {
      font-size: 1rem; } }
  .social a {
    border: 1px solid #949da5;
    border-radius: 1rem;
    padding: 0.25rem 0.75rem;
    transition: all 0.5s ease; }
    .social a i {
      margin-right: 0.25rem; }
    .social a:hover {
      color: white;
      background: #949da5; }
  .social a,
  .social a:visited:not(:hover) {
    color: #949da5;
    text-decoration: none; }
  .social a:not(:last-child) {
    margin-right: 0.5rem; }

.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: center; }

.particle-background {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -3; }

.content {
  background: radial-gradient(ellipse farthest-side at top left, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.8) 50%, rgba(255, 255, 255, 0) 100%);
  box-sizing: border-box;
  min-height: 100%;
  padding: 1rem 1rem 6rem;
  position: relative;
  z-index: 0; }
  @media only screen and (min-width: 768px) {
    .content {
      padding: 2rem 2rem 6rem; } }

.hero-text {
  font-size: 2.5rem;
  line-height: 1.2; }
  @media only screen and (min-width: 768px) {
    .hero-text {
      font-size: 5rem; } }

.title {
  font-weight: bolder; }

.social, .sub-1, .sub-2, .sub-3 {
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-name: up-in;
  display: block; }

.sub-1 {
  animation-delay: 0.3s;
  color: #6c7681; }

.sub-2 {
  animation-delay: 0.6s;
  color: #949da5; }

.sub-3 {
  animation-delay: 0.9s;
  color: #bdc3c8; }

.nav-fill {
  background: #495057;
  height: 100%;
  left: 0;
  position: absolute;
  pointer-events: none;
  top: 0;
  transition: all 1s ease;
  width: 0;
  z-index: -1; }

nav {
  font-size: 2rem;
  line-height: 1;
  margin-bottom: 2rem; }
  nav a, nav a:visited {
    color: #495057;
    display: block;
    padding: 1rem;
    position: relative;
    text-decoration: none;
    z-index: 1; }
    nav a span.chevron, nav a:visited span.chevron {
      color: white;
      display: inline-block;
      font-size: 1rem;
      font-weight: bolder;
      margin-left: 1rem;
      opacity: 0;
      transition: all 0s ease 0s;
      transform: translateY(0.5rem);
      vertical-align: middle; }
  @media only screen and (min-width: 768px) {
    nav a:hover {
      color: white; }
      nav a:hover span.chevron {
        opacity: 1;
        transform: translateY(0);
        transition: all 0.8s cubic-bezier(0, 0.3, 0, 0.99) 0.3s; }
      nav a:hover .nav-fill {
        width: 100%; } }
  nav a.k {
    border-left: 2px solid #495057; }
  nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0; }

.profile {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem; }
  @media only screen and (min-width: 768px) {
    .profile {
      flex-direction: row; } }
  .profile__photo {
    animation-duration: 0.5s;
    animation-fill-mode: both;
    animation-name: up-in;
    display: flex;
    flex-grow: 0;
    flex-shrink: 0;
    justify-content: center;
    margin-bottom: 3rem;
    position: relative; }
    @media only screen and (min-width: 768px) {
      .profile__photo {
        flex-basis: 20rem;
        margin-bottom: unset; } }
    .profile__photo__wrapper {
      border-radius: 50%;
      overflow: hidden; }
      @media only screen and (min-width: 768px) {
        .profile__photo__wrapper {
          height: 20rem; } }
    .profile__photo img {
      height: 16rem;
      transform: scale(1.1);
      width: 16rem; }
      @media only screen and (min-width: 768px) {
        .profile__photo img {
          height: 20rem;
          width: unset; } }
      .profile__photo img.unloaded {
        -webkit-filter: blur(10px);
        /* Safari 6.0 - 9.0 */
        filter: blur(10px); }
  .profile__text {
    animation-delay: 0.3s;
    animation-duration: 0.5s;
    animation-fill-mode: both;
    animation-name: up-in;
    background: radial-gradient(ellipse farthest-side at center, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.8) 75%, rgba(255, 255, 255, 0) 100%);
    font-size: 1.2rem; }
    @media only screen and (min-width: 768px) {
      .profile__text {
        font-size: 1.5rem;
        margin-left: 2rem; } }
    .profile__text a {
      border-bottom: 1px solid rgba(0, 0, 0, 0.1); }
    .profile__text h2 {
      font-weight: lighter; }
    .profile__text :first-child {
      margin-top: 0; }

#scrollIndicator {
  left: 50%;
  opacity: 1;
  position: absolute;
  top: calc(100% + 1.5rem);
  transform: translate(-50%, -50%);
  transition: all 0.5s ease; }

#scrollIndicator.no-indicator {
  opacity: 0; }

.projects {
  margin-bottom: 2rem; }

.project-card {
  background-size: cover;
  color: #fff;
  font-size: 1.5rem;
  padding: 1.5rem;
  position: relative;
  z-index: 1; }
  .project-card::before {
    background: rgba(0, 0, 0, 0.6);
    content: '';
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1; }
  .project-card::after {
    background: rgba(255, 255, 255, 0.3);
    content: '';
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -2; }
  .project-card__title {
    border-bottom: 4px solid #fff;
    font-size: 2.5rem;
    font-weight: bolder;
    margin-bottom: 4rem;
    padding-bottom: 1rem;
    width: fit-content;
    z-index: 1; }
  .project-card__subtitle {
    margin-bottom: 1rem; }
  .project-card__date {
    font-size: 0.75rem; }

@keyframes up-in {
  0% {
    opacity: 0;
    transform: translateY(-0.5rem); }
  100% {
    opacity: 1;
    transform: translateY(0); } }
